.Records {
  display: flex;
  text-align: center;
  align-items: center;
  color: burlywood;

  &__list {
    padding: 0 20px 10px 20px;
    text-align: left;
  }

  &__buttons{
    width: 50px;
    margin: 0 5px;

    border: 1px solid black;
    border-radius: 5px;
    background-color: white;

    cursor: pointer;
  }
}

.white{
  background-color: #fff;
}

.hover{
  background-color: burlywood;
}